<template>
  <svg class="svg-icon">
    <use :xlink:href="`#assets-${props.name}`" :fill="color" />
  </svg>
</template>
<script>
export default {
  name: "SvgIcon",
  props: {
    name: {
      type: String,
      required: true,
      default: "email",
    },
    color: {
      type: String,
      default: "white",
    },
  },
  setup(props) {
    return {
      props,
    };
  },
};
</script>
