<template>
  <div id="ViewTransition2">
    <div class="moveBox" ref="moveBox">
      <div class="verticalItem" ref="verticalItem">
        <slot></slot>
      </div>
      <div class="html" v-html="html" ref="html"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ViewTransition2",
  components: {},
  props: {
    speed: {
      type: Number,
      default: 200,
    },
  },
  data() {
    return {
      html: "",
    };
  },
  methods: {
    //复制HTML
    htmlCopy() {
      this.html = this.$refs.verticalItem.outerHTML;
      this.$refs.html.style.height =
        this.$refs.verticalItem.offsetHeight + "px";
      this.$refs.html.style.width = this.$refs.verticalItem.offsetWidth + "px";
    },

    move(moveStr) {
      if (moveStr != undefined) {
        if (moveStr == "left") {
          this.moveFunc("left", 100);
        } else if (moveStr == "right") {
          this.moveFunc("left", -100);
        } else if (moveStr == "top") {
          this.moveFunc("top", 100);
        } else if (moveStr == "bottom") {
          this.moveFunc("top", -100);
        } else {
          console.error("moveStr is not allow");
        }
      } else {
        console.error("moveStr is undefined");
      }
    },
    moveFunc(direction, num) {
      let secondsNum;
      if (num > 0) {
        secondsNum = -num;
      } else if (num < 0) {
        secondsNum = Math.abs(num);
      }
      this.htmlCopy();
      this.$refs.verticalItem.style[direction] = num + "%";
      //开始移动
      this.$refs.moveBox.style.transition = this.speed / 1000 + "s";
      this.$refs.moveBox.style[direction] = secondsNum + "%";
      let that = this;
      setTimeout(function() {
        that.$refs.moveBox.style.transition = "";
        that.$refs.verticalItem.style[direction] = "0";
        that.html = "";
        that.$refs.moveBox.style[direction] = "0";
      }, this.speed);
    },
  },
};
</script>

<style scoped>
#ViewTransition2 {
  /* overflow: hidden; */
  position: relative;
  border: 1px solid red;
}
.moveBox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.verticalItem {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
}
.html {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid rgb(0, 4, 255);
}
</style>
