<template>
  <div id="inputItem">
    <div class="formBox" :style="{ width: width + '%' }">
      <h2 class="text">
        <div class="icon">
          <slot name="icon"></slot>
        </div>

        <slot name="text"></slot>
      </h2>
      <div class="input">
        <input
          :style="{ color: color, width: width }"
          @input="onInput"
          ref="input"
          :value="modelValue"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "InputItem",
  props: {
    modelValue: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    name: {
      type: String,
      default: "",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    maxlength: {
      type: String,
      default: "60",
    },
    inputStatus: {
      type: Boolean,
      default: true,
    },
    inputIsOk: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "Please Enter Your Email Address",
    },
    color: {
      type: String,
      default: "#000",
    },
    width: {
      type: String,
      default: "100",
    },
    error: {
      type: Boolean,
      default: false,
    },
    background: {
      type: String,
      default: "#fff",
    },
  },
  methods: {
    onInput(e) {
      this.$emit("update:modelValue", e.target.value);
    },
    inputStyle() {
      this.$refs.input.style.background = this.background;
    },
  },
  mounted() {
    this.inputStyle();
  },
};
</script>

<style scoped>
input {
  outline: none;
  width: 100%;
  border-radius: 6px;
  background: #fff;
  height: 40px;
  font-size: 18px;
  padding-left: 20px;
  box-sizing: border-box;
  border: none;
  margin: 4px 0;
}
input:focus {
  color: dodgerblue;
  transition: 0.2s;
  box-shadow: 0 0 8px 0 dodgerblue;
}
.text {
  display: flex;
  color: #fff;
  font-size: 20px;
  align-items: center;
  margin-bottom: 3px;
  margin-left: 14px;
}
.formBox {
  position: relative;
  /* margin-bottom: 20px; */
  box-sizing: border-box;
  min-width: 100px;
}
.errorMessage {
  position: absolute;
  bottom: -26px;
  right: 20px;
  color: red;
}
.icon {
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrong {
  border: 2px solid red;
  box-shadow: 0 0 8px 0 red;
  border-radius: 10px;
  animation: wrong 800ms ease-in-out;
}
@keyframes wrong {
  /* 水平抖动，核心代码 */
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%,
  80% {
    transform: translate3d(+2px, 0, 0);
  }
  30%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%,
  60% {
    transform: translate3d(+4px, 0, 0);
  }
  50% {
    transform: translate3d(-4px, 0, 0);
  }
}
</style>
