<template>
  <div id="DrowpDown">
    <div class="tittleBox" @click="tittleBoxClick">
      <slot name="slot0">
        <div class="tittle">
          <div class="tittleItem"></div>
          <div class="tittleItem">
            <span>{{ tittle }}</span>
          </div>
        </div>
      </slot>
    </div>
    <div class="itemListBox" ref="itemListBox">
      <div class="itemList" ref="itemList">
        <div
          class="item"
          v-for="(item, index) in newList"
          :key="index"
          :ref="'item' + index"
          @click="itemBox(index)"
        >
          <div class="itemBox" :ref="'itemBox' + index">
            <slot :name="'slot' + index + 1">
              <div class="slotBox">
                <span :ref="'span' + index">{{ item }}</span>
              </div>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DrowpDown",
  components: {},
  props: {
    tittle: {
      type: String,
      default: "tittleText",
    },
    list: {
      default: [
        "item1",
        "item2",
        "item3",
        "item4",
        "item1",
        "item2",
        "item3",
        "item4",
      ],
    },
    keys: {
      type: String,
      default: undefined,
    },
    drowp: {
      type: Boolean,
      default: false,
    },
    // default: {
    //   type: Boolean,
    //   default: true,
    // },
  },
  data() {
    return {
      drowpSwitch: false,
      newList: [],
    };
  },
  created() {
    if (this.list.length > 0) {
      this.dataInit();
    }
  },
  mounted() {
    if (this.list.length > 0) {
      this.switch(this.drowp);
      //   if (this.default) {
      //     this.click(0);
      //   }
    }
  },
  methods: {
    //数据初始化
    dataInit() {
      if (typeof this.list[0] == "string" || typeof this.list[0] == "number") {
        this.newList = this.list;
      } else {
        if (this.keys == undefined) {
          for (let i = 0; i < this.list.length; i++) {
            let item = "slot" + i;
            this.newList.push(item);
          }
        } else {
          for (let i = 0; i < this.list.length; i++) {
            if (this.list[i][this.keys] != undefined) {
              this.newList.push(this.list[i][this.keys]);
            } else {
              let item = "slot" + i;
              this.newList.push(item);
            }
          }
        }
      }
    },
    switch(boolean) {
      this.drowpSwitch = boolean;
      if (this.drowpSwitch) {
        this.$refs.itemListBox.style.height =
          this.$refs.itemList.offsetHeight + "px";
      } else {
        this.$refs.itemListBox.style.height = "0px";
      }
    },
    tittleBoxClick() {
      this.drowpSwitch = !this.drowpSwitch;
      if (this.drowpSwitch) {
        this.$refs.itemListBox.style.height =
          this.$refs.itemList.offsetHeight + "px";
      } else {
        this.$refs.itemListBox.style.height = "0px";
      }
    },
    itemBox(index) {
      let obj = {
        index: index,
        row: this.list[index],
      };
      this.$emit("return", obj);
      for (let i = 0; i < this.list.length; i++) {
        if (index == i) {
          this.$refs["itemBox" + i].style.background = "rgb(245, 247, 250)";
          this.$refs["span" + i].style.color = "rgb(52, 135, 240)";
        } else {
          this.$refs["itemBox" + i].style.background = "white";
          this.$refs["span" + i].style.color = "#000";
        }
      }
    },
    //第幾個被點擊
    click(index) {
      this.$refs["item" + index].click();
    },
  },
};
</script>

<style scoped>
.tittleBox {
  cursor: pointer;
  background: rgb(245, 245, 245);
  transition: 0.2s;
  border-top: 1px solid rgb(213, 213, 213);
  border-bottom: 1px solid rgb(213, 213, 213);
}
.tittleBox:hover {
  background: rgb(229, 229, 229);
}
.tittle {
  padding: 7px 0;
  display: flex;
}
.tittleItem {
  margin-left: 5px;
}
.tittleItem span {
  color: #000;
}
.itemListBox {
  overflow: hidden;
  transition: 0.2s;
  height: 0px;
}
.itemBox {
  background: rgb(255, 255, 255);
  cursor: pointer;
  transition: 0.2s;
}
.itemBox:hover {
  background: rgb(245, 247, 250);
}
.slotBox {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 7px 0;
}
.slotBox span {
  color: #000;
}
.item {
  border-bottom: 1px solid rgb(237, 237, 237);
}
</style>
