<template>
  <div id="bubble2" ref="bubble2" @click.stop>
    <div
      class="viewBox"
      ref="viewBox"
      v-if="viewBoxShow"
      @mouseout="mouseout"
      @mouseover="mouseover"
    >
      <slot></slot>
    </div>
  </div>
</template>

<script>
import { Execute } from "@/components/xuFunction/execute";
export default {
  name: "Bubble2",
  components: {},
  props: {},
  data() {
    return {
      viewBoxShow: false,
      execute: new Execute(),
      mouseInside: false,
      executeOpen: false,
    };
  },
  beforeUnmount() {
    window.removeEventListener("click", this.mousePosition);
  },
  methods: {
    open(index) {
      this.executeOpen = true;
      let boolean = this.execute.sameValueOnlyOnce(index);
      this.boolean = boolean;
      if (boolean) {
        this.viewBoxShow = true;
        window.addEventListener("click", this.mousePosition);
      } else {
        this.close();
      }
    },
    mouseout() {
      this.mouseInside = false;
    },
    mouseover() {
      this.mouseInside = true;
    },
    close() {
      this.viewBoxShow = false;
      this.mouseInside = false;
      window.removeEventListener("click", this.mousePosition);
      this.execute.sameValueOnlyOnce(undefined);
    },
    mousePosition(e) {
      if (this.executeOpen && !this.mouseInside) {
        this.executeOpen = false;
        this.positionMove(e);
      } else {
        this.close();
      }
    },
    positionMove(e) {
      this.$refs.viewBox.style.left = "0px";
      this.$refs.viewBox.style.top = "0px";

      //滑鼠点击位置
      let mouseX = e.clientX;
      let mouseY = e.clientY;

      //目前窗体大小
      let windowW = window.innerWidth;
      let windowH = window.innerHeight;

      //泡泡元素大小
      let bubbleW = this.$refs.viewBox.offsetWidth;
      let bubbleH = this.$refs.viewBox.offsetHeight;

      //泡泡左上角的位置
      let bubbleL = this.$refs.viewBox.getBoundingClientRect().left;
      let bubbleT = this.$refs.viewBox.getBoundingClientRect().top;

      //   console.log("滑鼠点击位置", mouseX, mouseY);
      //   console.log("目前窗体大小", windowW, windowH);
      //   console.log("泡泡元素大小", bubbleW, bubbleH);
      //   console.log("泡泡左上角的位置", bubbleL, bubbleT);

      let moveX = mouseX - bubbleL;
      let moveY = mouseY - bubbleT;
      this.$refs.viewBox.style.left = moveX + "px";
      this.$refs.viewBox.style.top = moveY + "px";

      //判断有无超出窗口
      if (mouseX + bubbleW > windowW - 20) {
        this.$refs.viewBox.style.left = "0px";
        this.$refs.viewBox.style.left = moveX - bubbleW + "px";
      }
      if (mouseY + bubbleH > windowH - 20) {
        this.$refs.viewBox.style.top = "0px";
        this.$refs.viewBox.style.top = moveY - bubbleH + "px";
      }
    },
  },
};
</script>

<style scoped>
#bubble2 {
  position: absolute;
  width: 0px;
  height: 0px;
  top: 0;
  left: 0;
  z-index: 100;
}
.viewBox {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid red;
}
</style>
