<template>
  <div id="LifeGame">
    <div class="box">
      <div class="lifeGameBox">
        <div v-for="(item, index) in 100" :key="item" class="yBox">
          <div
            v-for="(items, indexs) in 150"
            :key="items"
            class="xBox"
            :ref="'itemBox' + indexs + '/' + index"
            @click="
              xyposition(indexs, index);
              xyposition2(indexs, index);
            "
          ></div>
        </div>
      </div>
    </div>

    <button>运行</button>
  </div>
</template>

<script>
export default {
  name: "LifeGame",
  components: {},
  data() {
    return {
      //初始值
      initValue: [
        {
          x: 72,
          y: 52,
        },
        {
          x: 73,
          y: 52,
        },
        {
          x: 74,
          y: 52,
        },
      ],

      //现存状态
      nowValue: [],
    };
  },
  mounted() {
    this.nowValue = this.initValue;
    this.init(this.initValue);
  },
  methods: {
    //初始狀態
    init(initValue) {
      for (let i = 0; i < initValue.length; i++) {
        let x = initValue[i].x;
        let y = initValue[i].y;
        this.xyposition(x, y);
      }
    },

    //生成列表

    //點擊後的反應
    xyposition(x, y) {
      //   console.log(x, y);
      let color = this.$refs["itemBox" + x + "/" + y].style.background;
      if (color == "") {
        this.$refs["itemBox" + x + "/" + y].style.background = "red";
      } else {
        this.$refs["itemBox" + x + "/" + y].style.background = "";
      }
    },

    //點擊後的反應2
    xyposition2(x, y) {
      //   let index = 0;
      //   this.nowValue.forEach((item) => {
      //     if (item.x == x && item.y == y) {
      //       console.log(index);
      //       this.nowValue.splice(index, 1);
      //     } else {
      //       let newItem = {
      //         x: x,
      //         y: y,
      //       };
      //       this.nowValue.push(newItem);
      //     }
      //     index++;
      //   });
      console.log(x, y);
      let j = 0;
      for (let i = 0; i < this.nowValue.length; i++) {
        let item = this.nowValue[i];
        if (item.x == x && item.y == y) {
          this.nowValue.splice(i, 1);
          break;
        } else {
          if (j == i) {
            let newItem = {
              x: x,
              y: y,
            };
            this.nowValue.push(newItem);
          }
        }
        j++;
      }
      console.log(this.nowValue);
    },

    //点击后更新列表
    updataForm() {},
  },
};
</script>

<style scoped>
#LifeGame {
}
.box {
  display: flex;
}
.lifeGameBox {
  border: 1px solid red;
}
.yBox {
  /* border: 1px solid red; */
  display: flex;
}
.xBox {
  width: 4px;
  height: 4px;
  /* border: 1px solid red; */
  cursor: pointer;
}
</style>
