<script>
import Bubble2 from "../../components/bubble/Bubble2.vue";
import Bubble from "../../components/bubble/Bubble.vue";
import PageStyleOne from "../../components/style/pageStyle/PageStyleOne.vue";
import SvgIcon from "../../xu-components/cell/svgIcon/SvgIcon.vue";
import { getQueryToJson } from "@/xu-components/javaScript/getQueryToJson";
import Mask from "../../xu-components/cell/mask/Mask.vue";
import LevelToggle from "../../xu-components/multi/levelToggle/LevelToggle.vue";
import ImgCorrection from "../../xu-components/cell/imgCorrection/ImgCorrection.vue";
import ImgView from "../../xu-components/multi/ImgView.vue";
import XuDebounce from "../../xu-components/cell/debounce/XuDebounce.vue";
import InputText from "../../components/form/InputText.vue";
import LifeGame from "../../components/lifeGame/LifeGame.vue";
import TabularForms from "../../components/tabularForms/TabularForms.vue";
import NavBar2 from "../../components/navbar/NavBar2.vue";
import TextAnimationOne from "@/components/animation/textAnimation/TextAnimationOne";
import DrowpDown from "@/components/drowpDown/DrowpDown";
import ViewTransition from "@/components/animation/viewTransition/ViewTransition";
import MountedTest from "./comp/MountedTest.vue";
import ViewTransition2 from "@/components/animation/viewTransition/ViewTransition2";

export default {
  name: "ThreePage",
  components: {
    PageStyleOne,
    SvgIcon,
    Mask,
    LevelToggle,
    ImgCorrection,
    ImgView,
    XuDebounce,
    InputText,
    LifeGame,
    TabularForms,
    NavBar2,
    TextAnimationOne,
    DrowpDown,
    ViewTransition,
    MountedTest,
    ViewTransition2,
    Bubble,
    Bubble2,
  },
  watch: {
    verifyEmailVo() {
      console.log(this.verifyEmailVo);
    },
  },
  data() {
    return {
      bubbleSwitch: false,
      bubbleSwitch2: false,
      bubbleSwitch3: false,
      bubbleSwitch4: false,
      NavBar2Arr: [
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第一个第一个第一个第一个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第二个第二个第二个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第三个第三个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第四个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第一个第一个第一个第一个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第二个第二个第二个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第三个第三个",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          spod: "feeee",
          num: 88,
        },
        {
          id: "1354wd6-3dw6648-wfqqfwee",
          name: "第四个",
          spod: "feeee",
          num: 88,
        },
      ],
      indexNNN: [],
      indexNNN2: [],
      ransitionItem: true,
      verifyEmailVo: "",
      validatorShow: false,
      img: [
        {
          id: "img0",
          url: require("../../components/debounce/img/debounce1.png"),
        },
        {
          id: "img1",
          url: require("../../components/debounce/img/debounce2.png"),
        },
        {
          id: "img2",
          url: require("../../components/debounce/img/debounce3.png"),
        },
      ],

      value: "x.com?id=1731009253527855883&wfr=spider&for=pc",

      maskOn: false, //点击显示弹窗

      tiem: 0, //url转成Json双击执行

      imgW: 100, //原始大小（放大縮小用）
      mouseW: Number,
      mouseH: Number,

      imgcode: "",
    };
  },
  mounted() {
    window.addEventListener("mousewheel", this.handleScroll); //監聽滾輪
  },
  unmounted() {
    window.removeEventListener("mousewheel", this.handleScroll); //  离开页面清除（移除）滚轮滚动事件
  },
  methods: {
    closeBubble() {
      this.$refs.Bubble2.close();
    },
    bubbleReturn2(boolean) {
      console.log(boolean);
    },
    testBtn111() {
      console.log("123");
    },
    rightBottomBoxItem() {
      this.bubbleSwitch4 = !this.bubbleSwitch4;
    },
    rightTopBoxItem() {
      this.bubbleSwitch3 = !this.bubbleSwitch3;
    },
    leftTopBoxItem(index) {
      this.$refs.Bubble2.open(index);
    },
    nnnaaaItem() {
      this.bubbleSwitch = !this.bubbleSwitch;
    },
    //前后差值比较
    differenceComparison(index) {
      let num;
      if (this.indexNNN[0] == undefined) {
        this.indexNNN[0] = index;
        num = undefined;
      } else {
        if (this.indexNNN[1] == undefined) {
          this.indexNNN[1] = index;
          num = this.indexNNN[1] - this.indexNNN[0];
        } else {
          this.indexNNN[0] = JSON.parse(JSON.stringify(this.indexNNN[1]));
          this.indexNNN[1] = index;
          num = this.indexNNN[1] - this.indexNNN[0];
        }
      }
      return num;
    },
    differenceComparison2(index) {
      let num;
      if (this.indexNNN2[0] == undefined) {
        this.indexNNN2[0] = index;
        num = undefined;
      } else {
        if (this.indexNNN2[1] == undefined) {
          this.indexNNN2[1] = index;
          num = this.indexNNN2[1] - this.indexNNN2[0];
        } else {
          this.indexNNN2[0] = JSON.parse(JSON.stringify(this.indexNNN2[1]));
          this.indexNNN2[1] = index;
          num = this.indexNNN2[1] - this.indexNNN2[0];
        }
      }
      return num;
    },
    drowpDownReturn(obj) {
      console.log(obj.row);
      let num = this.differenceComparison2(obj.index);
      if (num > 0) {
        this.ransitionItem = !this.ransitionItem;
        this.$refs.ViewTransition.move("top");
      } else if (num < 0) {
        this.ransitionItem = !this.ransitionItem;
        this.$refs.ViewTransition.move("bottom");
      }
    },

    navBar2Return(obj) {
      let num = this.differenceComparison(obj.index);
      if (num > 0) {
        this.ransitionItem = !this.ransitionItem;
        this.$refs.ViewTransition.move("left");
      } else if (num < 0) {
        this.ransitionItem = !this.ransitionItem;
        this.$refs.ViewTransition.move("right");
      }
    },
    ImgCorrection() {
      console.log("ImgCorrection被点击");
    },

    debonce(b) {
      //防抖方法
      console.log(b);
      console.log("debonceFunction执行");
    },

    getQuery(v) {
      //url转成Json
      getQueryToJson(v).then((json) => {
        console.log(json);
      });
    },
    twoClick() {
      //url转成Json双击执行
      this.tiem++;
      let that = this;
      if (this.tiem > 0) {
        setTimeout(function() {
          that.tiem = 0;
        }, 250);
      }
      if (this.tiem > 1) {
        getQueryToJson(this.value).then((json) => {
          console.log(json);
        });
      }
    },

    imgBoxF() {
      //点击显示弹窗
      this.maskOn = !this.maskOn;
    },

    move(e) {
      //監聽滑鼠位置
      let x = e.x;
      let y = e.y;
      let w = this.$refs.imgViewBox.offsetWidth;
      let h = this.$refs.imgViewBox.offsetHeight;
      let windowW = this.$store.state.windowSize;
      let windowH = this.$store.state.windowHeight;
      let absolutew = ((x - (windowW - w) / 2) / w) * 100;
      let absoluteh = ((y - (windowH - h) / 2) / h) * 100;
      let W = Math.round(absolutew);
      let H = Math.round(absoluteh);
      // console.log(W,H);
      console.log(W, H);
      this.mouseW = W;
      this.mouseH = H;
    },

    handleScroll(e) {
      //監聽滾輪
      let direction = e.deltaY > 0 ? "down" : "up";
      //   console.log(direction);
      if (direction == "up") {
        this.imgW = this.imgW + 10;
        this.$refs.imgViewBoxBigBox.style.width = this.imgW + "%";
        this.$refs.imgViewBoxBigBox.style.height = this.imgW + "%";
        let scrollTop =
          (this.$refs.imgViewBox.scrollHeight -
            this.$refs.imgViewBox.clientHeight) *
            (this.mouseH / 100) -
          ((100 - this.mouseH * 2) / 100) * this.$refs.imgViewBox.clientHeight;
        this.$refs.imgViewBox.scrollTop = scrollTop;
      } else if (direction == "down" && this.imgW > 100) {
        this.imgW = this.imgW - 10;
        this.$refs.imgViewBoxBigBox.style.width = this.imgW + "%";
        this.$refs.imgViewBoxBigBox.style.height = this.imgW + "%";
        let scrollTop =
          (this.$refs.imgViewBox.scrollHeight -
            this.$refs.imgViewBox.clientHeight) *
            (this.mouseH / 100) -
          ((100 - this.mouseH * 2) / 100) * this.$refs.imgViewBox.clientHeight;
        this.$refs.imgViewBox.scrollTop = scrollTop;
        console.log(this.$refs.imgViewBox.scrollTop);
      }
    },

    //上传文件
    fileUpLord(e) {
      let file = e.target.files[0];
      var reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        //获得64beat
        var imgcode = e.target.result;
        for (let i = 0; i < imgcode.length; i++) {
          console.log(imgcode.charAt(i));

          //加压完后显示图片
          if (i == imgcode.length - 1) {
            this.imgcode = imgcode;
          }
        }
      };
    },
  },
};
</script>

