<template>
  <div id="LevelToggle">
    <div class="leftBtn">
      <svg
        ref="leftBtn"
        @mousedown="mouseBtn('down', 'left')"
        @mouseup="mouseBtn('up', 'left')"
        t="1660611652469"
        class="leftSvg"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="8004"
        xmlns:xlink="http://www.w3.org/1999/xlink"
      >
        <path
          :fill="leftSvgFill"
          d="M512.5 953.005C760.2 953.005 961 754 961 508.5S760.2 64 512.5 64 64 263.014 64 508.5s200.8 444.505 448.5 444.505z m-0.5-834c217.048 0 393 174.381 393 389.5S729.046 898 512 898 119 723.618 119 508.5s175.951-389.493 393-389.493z"
          p-id="8005"
        ></path>
        <path
          d="M593.086 377.959L446.313 512.153 595.007 648.1l-0.01 0.006a24.436 24.436 0 0 1 7.854 17.775c0 14.147-12.541 25.613-28.012 25.613a29.352 29.352 0 0 1-19.444-7.181l-0.009 0.007-168.077-153.673 0.007-0.007a24.549 24.549 0 0 1-8.572-18.432v-0.055-0.058a24.552 24.552 0 0 1 8.572-18.431l-0.007-0.007 168.077-153.672 0.075 0.065a29.342 29.342 0 0 1 19.378-7.127c15.471 0 28.012 11.47 28.012 25.613a24.7 24.7 0 0 1-9.765 19.423z"
          p-id="8006"
        ></path>
      </svg>
    </div>
    <div class="itemBox" ref="itemBox">
      <div class="paddinItemBox" ref="paddinItemBox">
        <slot></slot>
      </div>
    </div>
    <div class="rightBtn">
      <svg
        ref="rightBtn"
        @mousedown="mouseBtn('down', 'right')"
        @mouseup="mouseBtn('up', 'right')"
        class="rightSvg"
        t="1660611668689"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="8140"
        xmlns:xlink="http://www.w3.org/1999/xlink"
      >
        <path
          :fill="rightSvgFill"
          d="M512.5 953.005C264.8 953.005 64 754 64 508.5S264.8 64 512.5 64 961 263.014 961 508.5 760.2 953.005 512.5 953.005z m0.5-834c-217.048 0-393 174.381-393 389.5S295.954 898 513 898s393-174.382 393-389.5-175.951-389.493-393-389.493z"
          p-id="8141"
        ></path>
        <path
          d="M431.914 377.959l146.773 134.194L429.993 648.1l0.01 0.006a24.436 24.436 0 0 0-7.854 17.775c0 14.147 12.541 25.613 28.012 25.613a29.352 29.352 0 0 0 19.444-7.181l0.009 0.007 168.077-153.673-0.007-0.007a24.549 24.549 0 0 0 8.572-18.432v-0.055-0.058a24.552 24.552 0 0 0-8.572-18.431l0.007-0.007-168.077-153.672-0.075 0.065a29.342 29.342 0 0 0-19.378-7.127c-15.471 0-28.012 11.47-28.012 25.613a24.7 24.7 0 0 0 9.765 19.423z"
          p-id="8142"
        ></path>
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  name: "LevelToggle",
  components: {},
  props: {
    speed: {
      type: Number,
      default: 1, //速度参数
    },
    type: {
      type: Number,
      default: 2, //1:长按滑动模式，2：按住滑动模式，3：流动换页模式
    },
    styleType: {
      type: Number,
      default: 2, //1:默认样式，2：disable样式
    },

    // 传出参数有 :location="location"
    // 可用来在父组件监听子组件目前的位置
  },
  data() {
    return {
      location: 0, //原始位置
      overlocation: 0, //最终位置
      timer: "", //每秒执行方法储存空间
      speedData: 3, //组件默认速度：不要动它，让他好好呆著就好
      leftSvgFill: "rgb(172, 172, 172)",
      rightSvgFill: "rgb(172, 172, 172)",
      debounce: true, //防抖参数
    };
  },
  created() {
    //速度参数矫验:可以为浮点数
    if (this.speed > 0) {
      this.speedData = this.speed * 3;
    } else {
      console.error("your <LevelToggle> speed can not <= 0");
    }

    //滑动类型参数矫验
    if (this.type > 3) {
      console.error("your <LevelToggle> type can not > 3");
    } else if (this.type < 1) {
      console.error("your <LevelToggle> type can not < 1");
    } else if (this.type.toString().indexOf(".") != -1) {
      console.error(
        "<LevelToggle> says you are so naughty : your type can not be float"
      );
    }

    //样式类型参数矫验
    if (this.styleType > 2) {
      console.error("your <LevelToggle> styleType can not > 2");
    } else if (this.styleType < 1) {
      console.error("your <LevelToggle> styleType can not < 1");
    } else if (this.styleType.toString().indexOf(".") != -1) {
      console.error(
        "<LevelToggle> says you are so naughty : your styleType can not be float"
      );
    }
  },
  mounted() {
    //初始化样式
    this.scroolStyle();
    //内框小于等于外框时隐藏按钮
    this.opacityBtn();
  },
  watch: {
    location() {
      this.scroolStyle();
      this.$emit("location", this.location);
    },
  },
  methods: {
    scroolStyle() {
      //样式控制
      if (this.styleType == 1) {
        //默认样式
        this.style1();
      } else if (this.styleType == 2) {
        //disable样式
        this.style2();
      }
    },
    //内框小于等于外框时隐藏按钮
    opacityBtn() {
      if (
        this.$refs.paddinItemBox.offsetWidth <= this.$refs.itemBox.offsetWidth
      ) {
        this.$refs.leftBtn.style.opacity = 0;
        this.$refs.leftBtn.style.cursor = "auto";
        this.$refs.rightBtn.style.opacity = 0;
        this.$refs.rightBtn.style.cursor = "auto";
      }
    },
    //默认样式
    style1() {
      if (this.location == 0) {
        this.$refs.leftBtn.style.opacity = 0;
        this.$refs.leftBtn.style.cursor = "auto";
      } else {
        this.$refs.leftBtn.style.opacity = 1;
        this.$refs.leftBtn.style.cursor = "pointer";
      }
      if (
        this.$refs.itemBox.scrollWidth - this.$refs.itemBox.clientWidth ==
        this.location
      ) {
        this.$refs.rightBtn.style.opacity = 0;
        this.$refs.rightBtn.style.cursor = "auto";
      } else {
        this.$refs.rightBtn.style.opacity = 1;
        this.$refs.rightBtn.style.cursor = "pointer";
      }
    },
    //disable样式
    style2() {
      if (this.location == 0) {
        this.$refs.leftBtn.style.cursor = "no-drop";
      } else {
        this.$refs.leftBtn.style.cursor = "pointer";
      }
      if (
        this.$refs.itemBox.scrollWidth - this.$refs.itemBox.clientWidth ==
        this.location
      ) {
        this.$refs.rightBtn.style.cursor = "no-drop";
      } else {
        this.$refs.rightBtn.style.cursor = "pointer";
      }
    },

    mouseBtn(vertical, level) {
      if (this.type == 1) {
        //按住滑动模式
        this.moveTypeFunc1(vertical, level);
      } else if (this.type == 2) {
        //流动换页模式
        this.moveTypeFunc2(vertical, level);
      } else if (this.type == 3) {
        //瞬间换页模式
        this.moveTypeFunc3(vertical, level);
      }
    },

    //按住滑动模式
    moveTypeFunc1(vertical, level) {
      if (vertical == "down") {
        if (level == "left") {
          this.timer = setInterval(() => {
            this.$refs.itemBox.scrollLeft -= this.speedData;
            this.location = this.$refs.itemBox.scrollLeft;
            this.overlocation = this.$refs.itemBox.scrollLeft;
          }, 1);
        } else {
          this.timer = setInterval(() => {
            this.$refs.itemBox.scrollLeft += this.speedData;
            this.location = this.$refs.itemBox.scrollLeft;
            this.overlocation = this.$refs.itemBox.scrollLeft;
          }, 1);
        }
      } else {
        clearTimeout(this.timer);
      }
    },
    //流动换页模式
    moveTypeFunc2(vertical, level) {
      //防抖方法：以免使用者连续点击引发BUG
      if (this.debounce && vertical == "up") {
        this.debounce = false;
        let that = this;
        setTimeout(function() {
          that.debounce = true;
        }, 700); //速度被写死：可能会因为speed参数被条慢而追不上引发BUG，或者因为speed被调快，照成使用体验不好，后续必须依照使用者输入的参数做动态传参

        //流动换页逻辑
        let speed = this.speedData * 1.5;
        if (vertical == "up") {
          if (level == "left") {
            this.overlocation -= this.$refs.itemBox.offsetWidth;
            this.timer = setInterval(() => {
              this.location -= speed;
              if (this.location < this.overlocation || this.location < 0) {
                this.location = this.overlocation;
                this.$refs.itemBox.scrollLeft = this.location;
                this.location = this.$refs.itemBox.scrollLeft;
                this.overlocation = this.$refs.itemBox.scrollLeft;
                clearTimeout(this.timer);
              }
              this.$refs.itemBox.scrollLeft = this.location;
            }, 1);
          } else {
            this.overlocation += this.$refs.itemBox.offsetWidth;
            this.timer = setInterval(() => {
              this.location += speed;
              if (
                this.location > this.overlocation ||
                this.location >
                  this.$refs.itemBox.scrollWidth -
                    this.$refs.itemBox.clientWidth
              ) {
                this.location = this.overlocation;
                this.$refs.itemBox.scrollLeft = this.location;
                this.location = this.$refs.itemBox.scrollLeft;
                this.overlocation = this.$refs.itemBox.scrollLeft;
                clearTimeout(this.timer);
              }
              this.$refs.itemBox.scrollLeft = this.location;
            }, 1);
          }
        }
      }
    },
    //瞬间换页模式
    moveTypeFunc3(vertical, level) {
      if (vertical == "up") {
        if (level == "left") {
          this.overlocation -= this.$refs.itemBox.offsetWidth;
          this.$refs.itemBox.scrollLeft = this.overlocation;
          this.location = this.overlocation;
          if (this.overlocation < this.$refs.itemBox.scrollLeft) {
            this.overlocation = this.$refs.itemBox.scrollLeft;
            this.location = this.overlocation;
          }
        } else {
          this.overlocation += this.$refs.itemBox.offsetWidth;
          this.$refs.itemBox.scrollLeft = this.overlocation;
          this.location = this.overlocation;
          if (this.overlocation > this.$refs.itemBox.scrollLeft) {
            this.overlocation = this.$refs.itemBox.scrollLeft;
            this.location = this.overlocation;
          }
        }
      }
    },
  },
};
</script>

<style scoped>
#LevelToggle {
  overflow: hidden;
  display: flex;
}
.leftBtn,
.rightBtn {
  width: 25px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.leftBtn {
  box-shadow: 2px 0px 5px rgb(255, 255, 255);
}
.leftSvg {
  width: 20px;
  cursor: pointer;
  transition: 0.2s;
}
.leftSvg:hover {
  width: 25px;
  margin-left: 2.5px;
}
.rightBtn {
  box-shadow: -2px 0px 5px rgb(255, 255, 255);
}
.rightSvg {
  width: 20px;
  cursor: pointer;
  transition: 0.2s;
}
.rightSvg:hover {
  width: 25px;
  margin-right: 2.5px;
}

.itemBox {
  width: calc(100% - 50px);
  height: 100%;
  overflow: hidden;
  overflow-x: auto;
  display: flex;
}
.itemBox::-webkit-scrollbar {
  height: 0px;
}
.paddinItemBox {
  height: 100%;
}
</style>
