<template>
  <div id="XuDebounce" ref="XuDebounce" @click="btnMouseup(s)">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "XuDebounce",
  props: {
    s: {
      //防抖几秒
      type: Number,
      default: 3000,
    },
    prompt: {
      //提示开关
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      switch: true,
    };
  },
  methods: {
    btnMouseup(s) {
      //点击后执行防斗程序，并执行父组件的方法再传出
      if (this.switch) {
        this.switch = false;
        let that = this;
        setTimeout(function() {
          that.switch = true;
          if (that.prompt) {
            console.log(s / 1000 + "s计时完毕可再次点击");
          }
        }, s);
        this.$emit("debonce", "");
      } else {
        if (this.prompt) {
          console.error(s / 1000 + "s内连续点击无效");
        }
      }
    },
  },
};
</script>
