<template>
  <div id="navBar2">
    <div class="optionBox">
      <div class="optionBoxC" ref="optionBoxC">
        <div
          class="optionBtn"
          :ref="'optionBtn' + index"
          v-for="(item, index) in list"
          :key="index"
        >
          <div
            class="optionBtnInside"
            :ref="'optionBtnInside' + index"
            @click="optionBtn(index)"
          >
            <slot :name="'slot' + index">
              <span class="span">{{ item }}</span>
            </slot>
          </div>
        </div>
      </div>
    </div>
    <div class="lineBox" ref="lineBox">
      <div class="line" ref="line"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavBar2",
  components: {},
  props: {
    arr: {
      default: [
        "slot0",
        "slot1",
        "slot2",
        "slot3",
        "slot4",
        "slot5",
        "slot6",
        "slot7",
        "slot8",
        "slot9",
      ],
    },
    keys: {
      type: String,
      default: undefined,
    },
  },
  created() {
    if (this.arr.length > 0) {
      this.dataInit();
    }
  },
  mounted() {
    if (this.arr.length > 0) {
      this.initStyle();
      this.click(0);
    }
  },
  data() {
    return {
      list: [],
    };
  },
  methods: {
    //数据初始化
    dataInit() {
      if (typeof this.arr[0] == "string" || typeof this.arr[0] == "number") {
        this.list = this.arr;
      } else {
        if (this.keys == undefined) {
          for (let i = 0; i < this.arr.length; i++) {
            let item = "slot" + i;
            this.list.push(item);
          }
        } else {
          for (let i = 0; i < this.arr.length; i++) {
            if (this.arr[i][this.keys] != undefined) {
              this.list.push(this.arr[i][this.keys]);
            } else {
              let item = "slot" + i;
              this.list.push(item);
            }
          }
        }
      }
    },

    //初始樣式設定
    initStyle() {
      let optionBoxCWidth = this.$refs.optionBoxC.offsetWidth;
      this.$refs.lineBox.style.width = optionBoxCWidth + "px";
      for (let i = 0; i < this.arr.length; i++) {
        let optionBtnWidth = this.$refs["optionBtn" + i].offsetWidth;
        this.$refs["optionBtn" + i].style.width = optionBtnWidth + "px";
        let optionBtnHeight = this.$refs["optionBtn" + i].offsetHeight;
        this.$refs["optionBtn" + i].style.height = optionBtnHeight + "px";
      }
    },
    //按鈕
    optionBtn(index) {
      let optionBtnWidth = this.$refs["optionBtn" + index].offsetWidth;
      this.$refs.line.style.width = optionBtnWidth + "px";
      let position = 0;
      for (let i = 0; i < index; i++) {
        position += this.$refs["optionBtn" + i].offsetWidth;
      }
      this.$refs.line.style.left = position + "px";

      let obj = {
        index: index,
        form: this.arr[index],
      };
      this.$emit("return", obj);
    },

    //第幾個被點擊
    click(index) {
      this.$refs["optionBtnInside" + index].click();
    },
  },
};
</script>

<style scoped>
.optionBox {
  display: flex;
  height: 100%;
}
.optionBoxC {
  display: flex;
}
.optionBtn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.optionBtnInside {
  margin: 5px 20px;
  cursor: pointer;
}
.optionBtnInside span:hover {
  font-size: 17px;
  transition: 0.2s;
  color: rgb(63, 133, 236);
}
.optionBtnInside span {
  transition: 0.05s;
}
.lineBox {
  width: 100%;
  height: 1px;
  position: relative;
  background: rgb(232, 232, 232);
  overflow: hidden;
}
.line {
  transition: 0.3s;
  position: absolute;
  left: 100%;
  top: 0;
  height: 100%;
  background: rgb(63, 133, 236);
  border-radius: 10px;
}
.span {
  word-break: keep-all;
  white-space: nowrap;
  color: rgb(89, 89, 89);
}
</style>
