<template>
  <div id="ViewTransition" ref="ViewTransition">
    <div class="moveBox" ref="moveBox">
      <div class="levelBox">
        <div class="verticalBox"></div>
        <div class="verticalBox">
          <slot></slot>
        </div>
        <div class="verticalBox"></div>
      </div>
      <div class="levelBox">
        <div class="verticalBox">
          <slot></slot>
        </div>
        <div class="verticalBox" ref="verticalBoxCenter">
          <div class="verticalItem" v-if="verticalItemShow">
            <slot></slot>
          </div>
          <div v-html="html" ref="html"></div>
        </div>
        <div class="verticalBox">
          <slot></slot>
        </div>
      </div>
      <div class="levelBox">
        <div class="verticalBox"></div>
        <div class="verticalBox">
          <slot></slot>
        </div>
        <div class="verticalBox"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ViewTransition",
  components: {},
  props: {
    speed: {
      type: Number,
      default: 200,
    },
  },
  data() {
    return {
      html: "",
      verticalItemShow: true,
    };
  },
  methods: {
    //复制HTML
    htmlCopy() {
      this.html = this.$refs.verticalBoxCenter.children[0].outerHTML;
      this.$refs.html.style.height =
        this.$refs.verticalBoxCenter.children[0].offsetHeight + "px";
      this.$refs.html.style.width =
        this.$refs.verticalBoxCenter.children[0].offsetWidth + "px";
    },
    //移动
    move(moveStr) {
      let num;
      let direction;
      if (moveStr != undefined) {
        if (moveStr == "left") {
          num = -200;
          direction = "left";
          this.moveFunc(direction, num);
        } else if (moveStr == "right") {
          num = 0;
          direction = "left";
          this.moveFunc(direction, num);
        } else if (moveStr == "top") {
          num = -200;
          direction = "top";
          this.moveFunc(direction, num);
        } else if (moveStr == "bottom") {
          num = 0;
          direction = "top";
          this.moveFunc(direction, num);
        } else {
          console.error("moveStr is not allow");
        }
      } else {
        console.error("moveStr is undefined");
      }
    },
    moveFunc(direction, num) {
      this.htmlCopy();
      this.verticalItemShow = false;
      //开始移动
      this.$refs.moveBox.style.transition = this.speed / 1000 + "s";
      this.$refs.moveBox.style[direction] = num + "%";
      //移动结束
      let that = this;
      setTimeout(function() {
        that.$refs.moveBox.style.transition = "";
        that.verticalItemShow = true;
        that.$refs.moveBox.style[direction] = "-100%";
      }, this.speed);
    },
  },
};
</script>

<style scoped>
#ViewTransition {
  overflow: hidden;
  position: relative;
}
.moveBox {
  position: absolute;
  width: 300%;
  height: 300%;
  left: -100%;
  top: -100%;
}
.levelBox {
  height: calc(100% / 3);
  display: flex;
}
.verticalBox {
  width: calc(100% / 3);
  height: 100%;
}
.verticalItem {
  width: 100%;
  height: 100%;
}
</style>
