<template>
  <div id="ImgCorrection" ref="ImgCorrection">
    <img :src="src" @load="imgLoadedHork" ref="img" class="img" />
  </div>
</template>

<script>
export default {
  name: "ImgCorrection",
  components: {},
  props: {
    src: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      outsideHeight: 0,
      imgHeight: 0,
    };
  },
  methods: {
    imgLoadedHork() {
      this.outsideHeight = this.$refs.ImgCorrection.offsetHeight;
      this.imgHeight = this.$refs.img.offsetHeight;
      if (this.outsideHeight < this.imgHeight) {
        this.$refs.img.style.width = "auto";
        this.$refs.img.style.height = "100%";
      } else {
        this.$refs.img.style.width = "100%";
        this.$refs.img.style.height = "auto";
      }
    },
  },
};
</script>

<style scoped>
#ImgCorrection {
  display: flex;
  align-items: center;
  justify-content: center;
}
#ImgCorrection img {
  width: 100%;
}
</style>
