//将url如x.com?id=1731009253527855883&wfr=spider&for=pc里面的属性个别取出并转成JSON数据类型

export function getQueryToJson(url){
  let newJson = {}
  let withoutDomain = url.split('?')[1];
  let itemArray = withoutDomain.split('&')
  for(let i = 0; i < itemArray.length; i++){
    let item = itemArray[i].split('=')
    newJson[item[0]] = item[1]
  }
  return Promise.resolve(newJson)
}