class Execute {
  constructor() {
    this.value = undefined
    this.indexNNN = []
  }

  /**
   * @param {基本数据} value
   * @returns boolean
   *  相同不重复执行:第一次传入的数据如果与第二次传入的数据相同的话则返回false,相反的话返回true
   */
  sameValueOnlyOnce(value) {
    let switchV
    if (value != this.value) {
      this.value = value
      switchV = true
    } else {
      switchV = false
    }
    return switchV
  }

  /**
   * @param {索引值} index
   * @returns 后者剪去前者所得出的值
   *  向量差值比较器
   */
  differenceComparison(index) {
    let num
    if (this.indexNNN[0] == undefined) {
      this.indexNNN[0] = index
      num = undefined
    } else {
      if (this.indexNNN[1] == undefined) {
        this.indexNNN[1] = index
        num = this.indexNNN[1] - this.indexNNN[0]
      } else {
        this.indexNNN[0] = JSON.parse(JSON.stringify(this.indexNNN[1]))
        this.indexNNN[1] = index
        num = this.indexNNN[1] - this.indexNNN[0]
      }
    }
    return num
  }
}

export { Execute }
